import {Component, OnInit} from '@angular/core';
import {HttpService} from "../services/http.service";
import {MatDialog, MatSnackBar} from "@angular/material";
import {ProgressService} from "../services/progress.service";
import {ServerMatch} from "../shared/servermatch";
import {Router} from "@angular/router";
import {DataService} from '../services/data.service';
import {ConfirmComponent} from '../shared/confirm/confirm.component';
import {DialogUsage} from '../shared/enum/dialogUsage';

@Component({
  selector: 'app-main-page',
  templateUrl: './main-page.component.html',
  styleUrls: ['./main-page.component.css']
})
export class MainPageComponent implements OnInit {
  pageName = 'main-page';

  bed = {
    cur: 200,
    goal: 1000
  };
  ext = {
    cur: 6,
    goal: 200
  };

  elements = {
    settings: {imageUrl: ''},
    print: {imageUrl: ''},
    tools: {imageUrl: ''},
  };

  constructor(private httpService: HttpService, private dialog: MatDialog,
              private progressService: ProgressService, private router: Router,
              public dataService: DataService) {
    this.elements = {
      settings: {
        imageUrl: ServerMatch.STATIC + 'assets/images/settings.png'
      },
      print: {
        imageUrl: ServerMatch.STATIC + 'assets/images/print-logo.png'
      },
      tools: {
        imageUrl: ServerMatch.STATIC + 'assets/images/tools.png'
      }
    };
    // console.log(ServerMatch.STATIC);
  }

  ngOnInit() {
    this.dataService.checkUnfinishedProject()
      .then(res => {
        if (res && res === true) {
          const rmDialog = this.dialog.open(ConfirmComponent, {
            width: '450px',
            data: {
              usage: DialogUsage.UnfinishedPrinting,
            }
          });
          rmDialog.afterClosed().subscribe(status => {
            if (status) {
              return Promise.resolve({
                cd: this.dataService.gcodePrintingFileDirectory,
                line: this.dataService.gcodeUnfinishedLine,
              });
            }
          });
        }
      })
      .then(res => {
        if (res && res['cd']) {
          this.progressService.enable();
          this.httpService.post('print', {
            cd: res['cd'],
            line: res['line'] || 0,
            action: 'print'
          }).subscribe(
            data => {
              if (data['status'] === 'success') {
                this.progressService.disable();
                this.router.navigate([`/print`]);
              } else {
                console.error('problem occurred!', data);
              }
            }, err => {
              this.progressService.disable();
              console.error('network problems supposedly!', err);
            }
          )
        }
      })
      .catch(rej => {
        console.error('about unfinished project: ', rej);
      });

    this.dataService.temps.subscribe(data => {
      this.bed = data.bed;
      this.ext = data.ext;
    });
  }

  goToSelectUsbPage() {
    this.router.navigate([`/select`]);
  }

  goToToolsPage() {
    this.router.navigate(['/tools']);
  }

  goToSettingsPage() {
    this.router.navigate([`/settings`]);
  }

}



