import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {ProgressService} from './progress.service';
import {HttpService} from './http.service';

export interface Temperature {
  bed: {
    cur: number,
    goal: number,
  };
  ext: {
    cur: number,
    goal: number
  };
}

@Injectable()
export class DataService {
  datas = [
    'temps',
    'percentage',
  ];

  firstTouch = true;

  temps: BehaviorSubject<Temperature> = new BehaviorSubject<Temperature>({
    bed: {
      cur: 0,
      goal: 10,
    },
    ext: {
      cur: 0,
      goal: 10,
    },
  });
  tempsInterval;

  // currentPercentage = 0;
  percentage: BehaviorSubject<number> = new BehaviorSubject<number>(0);
  percentageInterval;

  currentPrintTime = 0;
  printTime: BehaviorSubject<number> = new BehaviorSubject<number>(0);
  printTimeInterval;

  gcodePrintingFileDirectory = 'some/default/directory';
  gcodeUnfinishedLine = 0;

  connectedWifi = '';
  ipList = [];

  constructor(private progressService: ProgressService, private httpService: HttpService) {
    this.setTemps();
    // setInterval(() => {
    //   this.percentage.next(this.currentPercentage ++);
    // }, 50)
  }

  setUpBasic(name) {
    if (name === 'percentage') {
      this.percentage.next(0);
    } else if (name === 'printTime') {
      this.currentPrintTime = 0;
      this.printTime.next(0);
    }
  }

  setIpList() {
    return new Promise((resolve, reject) => {
      this.httpService.post('ip', true).subscribe(
        data => {
          if (data && data['ips']) {
            this.ipList = data['ips'];
            resolve(true);
          }
        }, err => {
          reject(false);
        }
      );
    })
  }

  checkUnfinishedProject() {
    return new Promise((resolve, reject) => {
      this.httpService.post('print', {action: 'unfinished'}, true).subscribe(
        data => {
          if (data['status'] === 'success') {
            if (data['unfinished'] && data['unfinished']['exist']) {
              this.gcodePrintingFileDirectory = data['unfinished']['cd'];
              this.gcodeUnfinishedLine = data['unfinished']['line'];
              resolve(true);
            }
          }
        }, err => {
          reject(false);
        }
      )
    });
  }

  setHeat(data) {
    return new Promise((resolve, reject) => {
      this.httpService.post('heat', {
        field: data['kind'] === 'bed' ? 'bed' : 'hotend',
        action: data['goal'] === 0 ? 'cooldown' : 'heat',
        value: data['goal']
      }).subscribe(
        data => {
          if (data['status'] === 'success') {
            resolve(true);
          }
          else {
            reject(false);
          }
        }, err => {
          reject(false);
        }
      );
    })
  }

  changeInterval(name, activate = true) { // name = 'temps' | 'percentage' | 'printTime'
    if (!activate) {
      if (name === 'temps') {
        clearInterval(this.tempsInterval);
      } else if (name === 'percentage') {
        clearInterval(this.percentageInterval);
      } else if (name === 'printTime') {
        clearInterval(this.printTimeInterval);
      }
      // this.changeInterval(this[name]) // Don't know what it was doing exactly :|
    } else {
      if (name === 'temps') {
        this.setTemps();
      } else if (name === 'percentage') {
        this.setPercentage();
      } else if (name === 'printTime') {
        this.setPrintTime();
      }
    }
  }

  setTemps() {
    this.tempsInterval = setInterval(() => {
      if (this.firstTouch)
        this.progressService.enable();
      this.httpService.get('temperatures', true).subscribe(
        (data) => {
          this.temps.next({
            bed: data.bed,
            ext: data.ext
          });
          if (this.firstTouch) {
            this.progressService.disable();
            this.firstTouch = false;
          }
        }, (err) => {
          if (!this.firstTouch) {
            this.progressService.disable();
            this.firstTouch = true;
            console.log("server didn't send temperatures", err);
          }
        }
      );
    }, 3000);
  }

  setPercentage() {
    this.percentageInterval = setInterval(() => {
      this.httpService.post(`print`, {
        action: 'percentage'
      }, true).subscribe(
        data => {
          this.percentage.next(data['percentage']);
        }, err => {
          console.error('percentage error', err);
        }
      );
    }, 5000);
  }

  setPrintTime() {
    this.printTimeInterval = setInterval(() => {
      // let's consider it a number for now, then we'll deal with the date
      this.currentPrintTime++;
      this.printTime.next(this.currentPrintTime);
    }, 1000);
  }

  stringizedTime(t) {
    let days, hours, minutes, seconds;
    days = Math.floor(t / 86400);
    t -= days * 86400;
    hours = Math.floor(t / 3600) % 24;
    t -= hours * 3600;
    minutes = Math.floor(t / 60) % 60;
    t -= minutes * 60;
    seconds = t % 60;

    return [
      days,
      '-',
      (hours > 9 ? hours : ('0' + hours)),
      ':',
      (minutes > 9 ? minutes : ('0' + minutes)),
      ':',
      (seconds > 9 ? seconds : ('0' + seconds))
    ].join(' ');
  }

}



